.report {
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	background: rgba(0, 0, 0, .7);
	display: -webkit-flex;
	display: flex;
}

.report .report-inner {
	margin: 20px auto;
	background: white;
	width: 800px;
	max-width: 100vw;
	overflow: auto;
	border-radius: 4px;
	padding: 16px;
}

.close-report,
.print-report {
	display: block;
	width: 100%;
	text-align: right;
	color: var(--fucsia);
	cursor: pointer;
}

.print-report {
	text-align: left;
}

.report h4,
.report h3,
.report p {
	margin-bottom: 1em;
}

.report h3 {
	text-align: center;
}

.debt-summary,
.payment-schedule {
	width: 100%;
	overflow: auto;
	margin-bottom: 2em;
}

.report table {
	width: 100%;
	border-collapse: collapse;
}

.report thead,
.report tfoot {
	color: white;
	background: var(--purple);
}

.report td,
.report th {
	padding: 8px 16px;
	border-right: 1px solid #ccc;
	text-align: right;
}

.debt-summary thead tr:first-child th {
	font-size: 1.3em;
	padding: 16px 4px;
	text-align: center;
}

.report tbody tr:nth-child(even) {
	background: #eee;
}

.report tbody tr:hover {
	background: var(--lightpink);
}

.report tbody td:hover {
	background: var(--pink);
}
:root {
  /* --background: #fbf6ef;  APP BACKGROUND COLOR */
  --brown: #cfc7b8;
  --lightpink: #cdbae8;
  --purple: #1A509C;
  --fucsia: #b40c66;
  --gray: #fafafa;
  --violet: #33208e;
  /* --pink: #c24ba3; Add new card button color*/
}

body {
  padding: 0;
  margin: 0;
  background: var(--background);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Roboto", "Helvetica", sans-serif;
}

input {
  font-family: "Oswald";
}

.pointer {
  cursor: pointer;
}

.App {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.Header {
  margin: 52px auto 0 auto;
  width: 80vw;
  font-size: 3em;
  font-weight: bold;
  text-align: center;
  color: #000066;
  text-shadow: -1px 0 #91c9d0, 0 1px #91c9d0, 1px 0 #91c9d0, 0 -1px #91c9d0;

  font-family: "Playfair Display", serif;
  font-weight: 400;
  font-size: 60px;
  text-transform: uppercase;
  display: inline-block;
  line-height: 40px;
  margin-bottom: 20px;
}

.calculator {
  margin: 20px auto 0 auto;
  width: 90vw;
  padding: 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.cards-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  /* margin-right: 100px; */
  
}

.BalanceChart {
  width: 90%;
  height: 300px;
}

@media (max-width: 940px) {
  .Header,
  .calculator {
    width: 95vw;
  }

  .BalanceChart {
    height: 250px;
  }
}

@media (max-width: 768px) {
  .BalanceChart {
    height: 200px;
  }
}

@media (max-width: 450px) {
  .Header,
  .calculator {
    width: 98vw;
  }
}

@media print{
	.close-report, .print-report{
		display:none !important;
	}
	table{
		width:100%;
	}
	.report-inner{
		padding:8px;
		text-align:center;
	}
}
